<template>
  <v-app>
<v-card color="grey lighten-4">
      <v-navigation-drawer 
      absolute
      class="hidden-sm-and-up"
      v-model="sideNav">
      <v-toolbar flat>
        <v-list>
          <v-list-tile>
            <v-list-tile-title class="title">
              {{ appName }}
            </v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-list>
        <v-list-tile 
          v-for="item in menuItems" 
          :key="item.title"
          :to="item.link"
          >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-badge color="red" v-if="item.badge">
            <span slot="badge">{{ item.badge }}</span>
            <span>{{ item.title }}</span>
          </v-badge>
          <v-list-tile-content v-else>{{ item.title }}</v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>


<v-toolbar color="teal lighten-1" dark>
      <v-toolbar-side-icon 
        @click.stop="sideNav = !sideNav"
        class="hidden-sm-and-up"></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link 
          to="/"
          tag="span"
          style="cursor: pointer"
          >
          {{ appName }}
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn flat 
          v-for="item in menuItems" 
          :key="item.title"
          :to="item.link"
          >
          <v-icon left>{{ item.icon }}</v-icon>
          <v-badge color="red" v-if="item.badge">
            <span slot="badge">{{ item.badge }}</span>
            <span>{{ item.title }}</span>
          </v-badge>
          <span v-else>{{ item.title }}</span>
        </v-btn>
      </v-toolbar-items>  
    </v-toolbar>

</v-card>



    <v-content>

      <GmapMap
      :center="mapCenter"
      :zoom="mapZoom"
      map-type-id="roadmap"
      style="width: 100%; display: block; height: 100%;"
      :options="mapOptions"
    >
  <GmapMarker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.position"
      :icon="getIcon(m)"
      alignment="center"
      @click="display(index)"
    />
      </GmapMap>

    </v-content>


    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card >
        <v-card-title v-bind:color="dialog_color" >
          <span class="headline" >Station : {{station.name}}</span>


        </v-card-title>


        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>

              <v-flex xs6 sm6 md6>
                <v-text-field color="black"
                  :disabled="true"
                  v-model="station.pm25"
                  label="PM2.5 µg./m3"></v-text-field>
              </v-flex>
              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.pm10"
                  label="PM10 µg./m3"
                  required
                ></v-text-field>
              </v-flex>

              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.temp"
                  label="Temperature (°C)"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.humi"
                  label="Humidity (%)"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs6 sm6 md6>
                <v-text-field v-bind:color="text_color"
                  :disabled="true"
                  v-model="station.dtm"
                  label="Last Update"
                  required
                ></v-text-field>
              </v-flex>




            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="black" flat @click="dialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>


<script>
    import Vue from 'vue'
    import * as VueGoogleMaps from 'vue2-google-maps'


 Vue.use(VueGoogleMaps, {
   load: {
     key: 'AIzaSyAP643J8qbUflgApZNfsbfUeA1r3pZ5AGs',
     libraries: 'places', // This is required if you use the Autocomplete plugin
     // OR: libraries: 'places,drawing'
     // OR: libraries: 'places,drawing,visualization'
     // (as you require)

     //// If you want to set the version, you can do so:
     // v: '3.26',
   },

   //// If you intend to programmatically custom event listener code
   //// (e.g. `this.$refs.gmap.$on('zoom_changed', someFunc)`)
   //// instead of going through Vue templates (e.g. `<GmapMap @zoom_changed="someFunc">`)
   //// you might need to turn this on.
   // autobindAllEvents: false,

   //// If you want to manually install components, e.g.
   //// import {GmapMarker} from 'vue2-google-maps/src/components/marker'
   //// Vue.component('GmapMarker', GmapMarker)
   //// then disable the following:
   // installComponents: true,
 })


import axios from 'axios';
 
export default {
  name: 'App',
    data() {
        return {
            //api_url :'http://192.168.10.203:8081/api/v1/station',
            // api_url :'https://www.phitloktoday.com/api/v1/station',
            api_url :'https://www.iotplus.cloud/api/v1/station',
            markers :[],
            //     {aqi:109, position: { lat:16.808225,lng:100.291653}},
            //     {aqi:110, position: { lat:16.818225,lng:100.371653}},
            //     {aqi:111, position: { lat:16.798225,lng:100.201653}},
            //     {aqi:112, position: { lat:16.758225,lng:100.271653}},
            //     {aqi:113, position: { lat:16.845225,lng:100.271653}}
            // ],

            srcicon:[],
            mapOptions:{
                disableDefaultUI : true
            },
            dialog:false ,
            dialog_color:"rgb(168,224,95)",
            text_color:"rgb(255,255,255)",
            station_index : 0,
            station :{
              name : "--",
              serial : "--",
              pm25 : "--",
              pm10 : "--",
              temp : "--",
              humi : "--",
              dtm : "--",
            },
            bg_colors : ["rgb(47,155,0)", "rgb(168,224,95)", "rgb(255,215,0)", "rgb(255,169,104)", "rgb(254,106,105)"],
            txt_colors : ["rgb(106,236,50)", "rgb(0,139,0)", "rgb(165,127,35)", "rgb(178,34,34)", "	rgb(175,44,59)"],
            description : ["ดีมาก", "ดี", "ปานกลาง", "เริ่มมีผลต่อสุขภาพ", "มีผลกระทบต่อสุขภาพ"],
            style_modal : '',
            sideNav: false,
            appName: "Fire Alarm",
            menuItems: [
                // {icon: 'map', title: 'Map', link: '/map'},
                // {icon: 'people', title: 'Sponser', link: '/sponser'},
                //{icon: 'person', title: 'Profile', link: '/profile'},
                // {icon: '', title: 'About Us',  link: '/aboutus'}
            ],
            // LPN
            mapCenter: { lat: 13.7124112, lng: 100.6060072 },
            mapZoom : 15

            //PHS
            //mapCenter : {lat:16.818225, lng:100.271653}
            //mapZoon : 11

        }
    },
    mounted () {

        this.loadStation()
        setInterval(
            function(){
                this.loadStation()
            }.bind(this)
            ,60000
        )
    },
    methods : {
        display(i){
          this.dialog = true
          this.station_index = i
          this.station = this.markers[i]

          this.dialog_color = this.bg_colors[this.getScorePM25(this.station.pm25)]
          this.text_color = this.txt_colors[this.getScorePM25(this.station.pm25)]
        },
        loadStation(){
            axios.post(this.api_url,).then((response)=>{
                this.markers = []
                
                response.data.forEach((item)=>{
                    this.markers.push({
                        aqi:item.pm25,
                        pm10:item.pm10,
                        pm25:item.pm25,
                        temp:item.temp,
                        humi:item.humi,
                        serial:item.serial,
                        name:item.name,
                        dtm:item.dtm,
                        position:{ lat:parseFloat(item.lat), lng:parseFloat(item.lng)}
                    })
                    this.station = this.markers[this.station_index]
                })
            })
        },
        getIcon(m){

            return 'data:image/svg+xml;utf-8, \
      <svg width="40px" height="40px" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
        <circle  fill="'+ this.bg_colors[this.getScorePM25(m.pm25)]+'" cx="27.2" cy="27.2" r="27.2"/> \
        <text dx="27" dy="35" text-anchor="middle" fill="'+ this.txt_colors[this.getScorePM25(m.pm25)]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+m.pm25+'</text>\
    </svg>'


        },
        getScorePM25(pm25){
            if (pm25 <= 25)
                return 0
            else if (pm25 <= 50)
                return 1
            else if (pm25 <= 75)
                return 2
            else if (pm25 <= 150)
                return 3
            else 
                return 4

        }
    }
    
}
</script>
